import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { ElementDTO } from "../../dto/ElementDTO";
import { NodeDTO } from "../../dto/NodeDTO";
import { TextDTO } from "../../dto/TextDTO";


export interface ITreeSlice {
  rootNode: ElementDTO | null;
  allNodes: {
    [name: string]: ElementDTO | TextDTO | NodeDTO
  } | null
}

const initialState: ITreeSlice = {
  rootNode: null,
  allNodes: null
};

export const treeSlice = createSlice({
    initialState,
    name: 'treeSlice',
    reducers: {
      updateTree(state: ITreeSlice, action: PayloadAction<ITreeSlice>) {
        state.rootNode = action.payload.rootNode;
        state.allNodes = action.payload.allNodes;
      },

      clearTree(state: ITreeSlice) {
        state.rootNode = null;
        state.allNodes = null;
      }
    }
});
