import { TypedUseSelectorHook, useDispatch, useSelector } from "react-redux";
import { combineReducers, configureStore } from "@reduxjs/toolkit";
import { routeSlice } from "./slices/routeSlice";
import { projectApi } from "./api/project.api";
import { projectSlice } from "./slices/projectSlice";
import { treeSlice } from "./slices/treeSlice";
import { nodeSlice } from "./slices/nodeSlice";
import { pageApi } from "./api/page.api";
import { toolSlice } from "./slices/toolSlice";

const rootReducer = combineReducers({
	toolSliceReducer: toolSlice.reducer,
	routeSliceReducer: routeSlice.reducer,
	projectSliceReducer: projectSlice.reducer,
	treeSliceReducer: treeSlice.reducer,
	nodeSliceReducer: nodeSlice.reducer,
	[projectApi.reducerPath]: projectApi.reducer,
	[pageApi.reducerPath]: pageApi.reducer
});

const middlewares = [projectApi.middleware, pageApi.middleware];

export const store = configureStore({
	reducer: rootReducer,
	middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(...middlewares),
});

export const useAppDispatch = () => useDispatch<typeof store.dispatch>();
export const useAppSelector: TypedUseSelectorHook<ReturnType<typeof rootReducer>> = useSelector;