import "./PageChooser.scss";
import {Dropdown} from './../../../Dropdown/Dropdown';
import { useAppDispatch, useAppSelector } from "../../../../redux/store";
import { useEffect } from "react";
import { projectSlice } from "../../../../redux/slices/projectSlice";
import { useLazyGetTreeQuery } from "../../../../redux/api/page.api";
import { TreeDTO } from "../../../../dto/TreeDTO";
import { treeSlice } from "../../../../redux/slices/treeSlice";

export function PageChooser(): JSX.Element {
  const projectName = useAppSelector(state => state.projectSliceReducer.name);
  const pagePaths = useAppSelector(state => state.projectSliceReducer.pagePaths);
  const activePage = useAppSelector(state => state.projectSliceReducer.activePage);

  const [getTree] = useLazyGetTreeQuery();

  const dispatch = useAppDispatch();

  const choosePage = (page: string | number) => {
    if (typeof page === "number") return;
    dispatch(projectSlice.actions.setActiveProjectPage(page));
  }

  useEffect(() => {
    getTree(activePage)
      .then(res => {
        if (res.isSuccess) {
          if (res.data.ok) {
            const treeDTO = (res.data.treeDTO as TreeDTO);
            dispatch(treeSlice.actions.updateTree(treeDTO));
          } else {
            alert("Ты что-то не то делаешь");
          }
        } else {
          alert("Перезагрузи сервер");
        }
      })
  }, [activePage]);

  return (
    <div className="page-chooser">
      <div className="page-chooser__text">Слои</div>
      <Dropdown title="" pagesCount={pagePaths.length} pages={pagePaths} cb={choosePage} dropdownArrow />
    </div>
  )
}